.profile__avatar:hover 
{
     cursor: pointer;
}

.profile__avatar:hover:before
{
    position:absolute;
    top: 0;
    left: 0;
    width:120px;
    height:120px;
    border-radius: 60px;
    background-color: rgba(0, 0, 0, 0.8);
    background-image: url(../../../images/pen.svg);
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: 26px 26px;
    content: "";
}